.row {}

.rows__posts {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
}

.row__poster {
    object-fit: contain;
    width: 100%;
    max-height: 100px;
    margin-right: 10px;
    transition: transform 450ms;
}

.row__poster:hover {
    transform: scale(1.08);
    /*opacity: 1;*/
}